<template>
    <div class="wrap login-box">
        <div class="login-main">
            <div class="login-form-box3">
                <div class="tit">设置密码</div>
                <div class="sub-tit">强烈建议设置密码，多设备登录更便捷</div>
                <form name="setPswForm">
                    <ul class="login-form-list">
                        <li class="set-psw-item psw-item">
                            <input :type="pswIptType"
                                   v-input-focus=""
                                   maxlength="16"
                                   minlength="6"
                                   v-pattern="/^[0-9a-zA-Z]{6,16}$/"
                                   v-required="true"
                                   name="password"
                                   v-model="password"
                                   placeholder="6~16个字符，支持大小写字母和数字">
                            <span class="eye"
                                  :class="{'eye-open':pswIptType==='text','eye-close':pswIptType==='password'}"
                                  @mouseout="hoverEye('2')"
                                  @mouseover="hoverEye('1')"></span>
                        </li>
                        <li class="set-confirmpsw-item psw-item">
                            <input :type="pswIptType"
                                   v-input-focus=""
                                   maxlength="16"
                                   minlength="6"
                                   v-pattern="/^[0-9a-zA-Z]{6,16}$/"
                                   v-required="true"
                                   name="confirmPassword"
                                   v-model="confirmPassword"
                                   placeholder="请再次输入密码">
                            <span class="eye"
                                  :class="{'eye-open':pswIptType==='text','eye-close':pswIptType==='password'}"
                                  @mouseout="hoverEye('2')"
                                  @mouseover="hoverEye('1')"></span>
                        </li>
                        <li>
                            <button class="login-btn"
                                    :style="(setPswForm.$invalid || submitAble)?{background:'#ddd',cursor:'not-allowed'}:{}"
                                    @click="setPsw($event,setPswForm.$invalid)">保&nbsp;&nbsp;存</button>
                        </li>
                        <li class="status-item">
                            <div class="not-tip" @click="changeNotTipSetPsw">
                            <span class="chx"
                                  :class="{'checked':notTipSetPsw==='true'}"></span>
                                不再提示
                            </div>
                            <a href="javascript:void(0)"
                               @click="goCenter"
                               class="jump">跳过&gt;</a>
                        </li>
                    </ul>
                </form>
            </div>
        </div>
        <!-- <div class="login-ballbg-sm" style="z-index:1"></div>
        <div class="login-ballbg-lg" style="z-index:1"></div> -->
    </div>
</template>

<script>
    // 缓存一个月
    let t = 24 * 60 * 60 * 30 * 1000
    let expires = new Date().getTime() + t
    import Cookie from 'cookie';
    import helper from '@/assets/js/helper'
    import {mapActions} from 'vuex'
    import {initFormErrorObj} from '@/assets/js/validationPlugin'
    let cookie = Cookie.parse(document.cookie);
    export default {
        data: () => ({
            ...initFormErrorObj('setPswForm', ['password', 'confirmPassword']),//设置密码表单
            notTipSetPsw:'false',
            password:'',
            confirmPassword:'',
            submitAble:false,
            pswIptType:'password',
            accountInfo:{}
        }),
        computed: {
            isLogin(){
                return (this.$store.getters['account/status'] === 1);
            }
        },
        methods: {
            ...mapActions('account', ['initPasswordActions']),
            hoverEye(type){
                if(type==='1'){
                    this.pswIptType = 'text'
                }else{
                    this.pswIptType = 'password'
                }
            },
            setPsw(e,invalid){
                e.preventDefault()
                if (invalid) {
                    window.console.log('表单没过');
                    return
                }
                if(this.submitAble){
                    return
                }
                if(this.confirmPassword!==this.password){
                    this.$message.warning('确认密码必须和密码一致')
                    return
                }
                this.submitAble = true
                let params = {
                    password:this.password
                }
                this.initPasswordActions(params).then((res)=>{
                    let {error_code, data} = res;
                    if(!error_code){
                        this.$message.success('密码设置成功，3秒后自动登录')
                        setTimeout(()=>{
                            this.submitAble = false
                            this.goCenter()
                        },3000)
                    }else{
                        this.submitAble = false
                    }
                },()=>{
                    this.submitAble = false
                })
            },
            changeNotTipSetPsw(){
                if(this.notTipSetPsw==='true'){
                    this.notTipSetPsw = 'false'
                }else{
                    this.notTipSetPsw = 'true'
                }
                let phone = this.accountInfo.phone
                if(phone){
                    let key = 'notTipSetPsw' + phone
                    helper.cookieUtils.setItem(key,this.notTipSetPsw,expires)
                }
//                document.cookie = Cookie.serialize('notTipSetPsw', this.notTipSetPsw, {path: "/",expires});
            },
            goCenter(){
                let remeberPsw = helper.cookieUtils.getItem('remeberPsw')
                let hasExpires = remeberPsw === 'true' ? true : false
                var student_list = helper.cookieUtils.getItem('student_list',hasExpires)
                if (student_list && student_list.length) {
                    this.$router.push({
                        name: 'sign_in_success'
                    })
                } else {
                    this.$router.push({name: 'loginSuccess'});
                }
            }
        },
        components:{

        },
        mounted(){
            let root = document.getElementsByTagName('html')[0]
            root.setAttribute('class','loginRoot')
            if(cookie.accountInfo){
                this.accountInfo = JSON.parse(cookie.accountInfo);
                let phone = this.accountInfo.phone
                if(phone){
                    let key = 'notTipSetPsw' + phone
                    helper.cookieUtils.setItem(key,this.notTipSetPsw,expires)
                }
            }else{
                this.goView('sign_in')
            }
//            document.cookie = Cookie.serialize('notTipSetPsw', this.notTipSetPsw, {path: "/"});
        },
        created(){
            let root = document.getElementsByTagName('html')[0]
            root.setAttribute('class','loginRoot')
        },
        destroyed(){
            let root = document.getElementsByTagName('html')[0]
            root.setAttribute('class','')
        }
    }
</script>

<style lang="postcss" scoped>
    .btn-login {
        width: 100%;
        font-size: 1.1rem;

        &
        span {
            display: inline-block;
            letter-spacing: 2rem;
        }

    }
    .login-tab-item{
        cursor: pointer;
    }
</style>
