<template>
    <div class="dialog" v-show="showDialog">
        <div class="dialog-mask" v-show="showMask"></div>
        <div class="dialog-main verify-box">
            <div class="dialog-header">
                <span class="close" @click="close">x</span>
            </div>
            <div class="dialog-content verify-main">
                <form name="verifyForm">
                    <ul class="verify-form-list">
                        <li class="code-item">
                            <span>校验码</span>
                            <img :src="codeObj.captcha_image" alt="">
                            <a href="javascript:void(0)" @click="change">看不清，换一张</a>
                        </li>
                        <li class="verify-item">
                            <input type="text"
                                   v-input-focus
                                   @input="inputCode"
                                   v-required="true"
                                   v-pattern="/.{4}/"
                                   maxlength="4"
                                   name="code"
                                   v-model="modelCode"
                                   placeholder="请输入图片中的校验码">
                            <!--<div class="wrong-tips"
                                 v-if="showError">校验码错误</div>-->
                        </li>
                        <li>
                            <button class="login-btn"
                                    @click="sendVerifyCaptcha($event,verifyForm.$invalid)"
                                    :style="verifyForm.$invalid?{background:'#ddd',cursor:'not-allowed'}:{}">确  定</button>
                        </li>
                    </ul>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    import {initFormErrorObj,initForm} from '@/assets/js/validationPlugin'
    import {mapActions} from 'vuex'
    export default {
        props: {
            show: {
                type: Boolean
            },
            showMask:{
                type: Boolean,
                default:true
            },
            dialogStyle:{
                type:Object
            }
        },
        mounted() {

        },
        data() {
            return {
                ...initFormErrorObj('verifyForm', ['code']),
                codeObj:{

                },
                submitAble:false,
                modelCode:'',
                sendingCode:false,
                showError:false
            };
        },
        watch: {
            showDialog:{
                handler(nv){
                    if(nv){
                        this.initSome()
                        this.freshCaptcha()
                    }
                },
                immediate:true
            }
        },
        computed:{
            showDialog(){
                return this.show
            }
        },
        methods: {
            ...mapActions('account', ['freshCaptchaActions','verifyCaptchaActions']),
            filterNotNumberOrEn($event){
                //去除空格，去除非数字
                let value = $event.target.value.replace(/^\s+/g, '').replace(/\s+$/g, '')
                let nValue = ''
                for (let i = 0; i < value.length; i++) {
                    let item = value.charAt(i)
                    if (/^[\da-zA-Z]$/g.test(item)) {
                        nValue += item
                    }
                }
                return nValue
            },
            //过滤掉非数字非英文和空格
            inputCode($event) {
                let nValue = this.filterNotNumberOrEn($event)
                this.modelCode = nValue
            },
            initSome(){
                this.modelCode = undefined
                this.codeObj = {}
                this.showError = false
                /*setTimeout(()=>{
                    initForm('verifyForm',this)
                },2000)*/
                setTimeout(()=>{
                    initForm('verifyForm',this)
                })
            },
            sendVerifyCaptcha(e,invalid) {
                e.preventDefault()
                if (this.sendingCode || invalid || !this.codeObj.captcha_token) {
                    return
                }
                this.sendingCode = true
                this.sendVerifyCaptchaRequest()
            },
            sendVerifyCaptchaRequest() {
                let params = {
                    captcha_code:this.modelCode,
                    captcha_token:this.codeObj.captcha_token
                }
                this.verifyCaptchaActions(params).then((res) => {
                    this.sendingCode = false
                    let {error_code} = res;
                    if (!error_code) {
                        window.console.log('图形验证码校验成功');
                        this.showError = false
                        this.$emit('close')
                        this.$emit('successCb')
                    }else{
                        this.showError = true
                        this.change()
                    }
                }, (err) => {
                    this.sendingCode = false
                })
            },
            freshCaptcha(){
                this.freshCaptchaActions().then((res)=>{
                    this.submitAble = false
                    let {error_code, data} = res;
                    if(!error_code){
                        this.codeObj = data
                    }
                },()=>{
                    this.submitAble = false
                })
            },
            change(){
                if(this.submitAble){
                    return
                }
                this.modelCode=''
                this.freshCaptcha()
            },
            submit(){

            },
            close(){
                this.$emit('close')
            }
        }
    };
</script>
<style lang="postcss" scoped>

</style>