<template>
    <div class="wrap login-box">
        <div class="login-main">
            <div class="login-form-box4">
                <div class="tit">重置密码</div>
                <form name="resetPswForm">
                    <ul class="login-form-list">
                        <li class="phone-item">
                            <input type="text"
                                   maxlength="11"
                                   @input="inputPhone"
                                   name="phone"
                                   v-required="true"
                                   v-pattern="/^\d{11}$/"
                                   v-model="resetModel.phone"
                                   placeholder="请输入手机号码"
                                   v-input-focus="">
                        </li>
                        <li class="psw-item">
                            <input :type="pswIptType"
                                   name="password"
                                   v-model="resetModel.password"
                                   maxlength="16"
                                   minlength="6"
                                   v-pattern="/^[0-9a-zA-Z]{6,16}$/"
                                   v-required="true"
                                   placeholder="6~16个字符，支持大小写字母和数字"
                                   v-input-focus="">
                            <span class="eye"
                                  :class="{'eye-open':pswIptType==='text','eye-close':pswIptType==='password'}"
                                  @mouseout="hoverEye('2')"
                                  @mouseover="hoverEye('1')"></span>
                        </li>
                        <li class="code-item">
                            <input type="text"
                                   @input="inputCode"
                                   maxlength="4"
                                   v-pattern="/^.{4}$/"
                                   name="phoneCode"
                                   v-required="true"
                                   v-model="resetModel.phoneCode"
                                   placeholder="请输入短信验证码"
                                   v-input-focus="">
                            <a href="javascript:void(0)"
                               @click="openImgVerifyDialog(resetPswForm.phone.$invalid)"
                               :style="(sendingCode || resetPswForm.phone.$invalid)?{color:'#ddd',cursor:'not-allowed'}:{}">{{getSendCodeBtnStr}}</a>
                        </li>
                        <li>
                            <button class="login-btn"
                                    :style="(resetPswForm.$invalid || submitAble)?{background:'#ddd',cursor:'not-allowed'}:{}"
                                    @click="resetPsw($event,resetPswForm.$invalid)">保&nbsp;&nbsp;存</button>
                        </li>
                        <li class="return">
                            <a href="javascript:void(0)" @click="$router.push('sign_in')">返回&gt;</a>
                        </li>
                    </ul>
                </form>
            </div>
        </div>


        <img-verify :show="showVerifyDialog"
                    @successCb="sendPhoneCode"
                    @close="closeVerifyDialog"></img-verify>

    </div>
</template>

<script>
    import {initFormErrorObj} from '@/assets/js/validationPlugin'
    import {mapActions} from 'vuex'
    import imgVerify from '@/common/imgVerify.vue'
    let sendCodeCount = 60
    export default {
        data: () => ({
            ...initFormErrorObj('resetPswForm', ['phone','password','phoneCode']),//验证码登录表单
            resetModel:{
                phone:'',
                password:'',
                phoneCode:''
            },
            submitAble:false,
            showVerifyDialog:false,
            sendCodeCount: sendCodeCount,
            sendingCode: false,
            sendCodeOnces: true,//是否第一次发送验证码
            pswIptType:'password'
        }),
        computed: {
            getSendCodeBtnStr() {
                let str = ''
                if (!this.sendingCode && this.sendCodeOnces) {
                    str = '发送验证码'
                } else if (!this.sendingCode && !this.sendCodeOnces) {
                    str = '重发验证码'
                } else if (this.sendingCode) {
                    str = `${this.sendCodeCount}秒后可重发`
                }
                return str
            }
        },
        methods: {
            ...mapActions('account', ['resetPasswordActions','sendPhoneCodeActions']),
            hoverEye(type){
                if(type==='1'){
                    this.pswIptType = 'text'
                }else{
                    this.pswIptType = 'password'
                }
            },
            closeVerifyDialog(){
                this.showVerifyDialog = false
            },
            filterNotNumber($event){
                //去除空格，去除非数字
                let value = $event.target.value.replace(/^\s+/g, '').replace(/\s+$/g, '')
                let nValue = ''
                for (let i = 0; i < value.length; i++) {
                    let item = value.charAt(i)
                    if (/^\d$/g.test(item)) {
                        nValue += item
                    }
                }
                return nValue
            },
            //输入手机号过滤掉非数字和空格
            inputPhone($event) {
                let nValue = this.filterNotNumber($event)
                this.resetModel.phone = nValue
            },
            inputCode($event) {
                let nValue = this.filterNotNumber($event)
                this.resetModel.phoneCode = nValue
            },
            resetPsw(e,invalid){
                e.preventDefault()
                if(this.submitAble || invalid){
                    return
                }
                let params = {
                    phone:this.resetModel.phone,
                    password:this.resetModel.password,
                    verification_code:this.resetModel.phoneCode
                }
                this.submitAble = true
                this.resetPasswordActions(params).then((res)=>{
                    let {error_code, data} = res;
                    if (!error_code) {
                        this.$message.success('密码重置成功，3秒后返回登录页面')
                        setTimeout(()=>{
                            this.submitAble = false
                            this.$router.push({
                                name:'sign_in'
                            })
                        },3000)
                    }else{
                        this.submitAble = false
                    }
                },()=>{
                    this.submitAble = false
                })
            },
            openImgVerifyDialog(phoneInvalid){
                if (this.sendingCode || phoneInvalid) {
                    return
                }
                if(this.sendCodeOnces){
                    this.sendPhoneCode()
                }else{
                    this.showVerifyDialog = true
                }
            },
            sendPhoneCodeRequest() {
                let params = {
                    phone_num: this.resetModel.phone,
                    use_type:2
                }
                this.sendPhoneCodeActions(params).then((res) => {
                    this.setInterval()
                    let {error_code} = res;
                    if (!error_code) {
                        this.$message.success('验证码发送成功')
//                        window.console.log('发送验证码成功');
                    }
                }, (err) => {
                    this.clearInterval()
                })
            },
            sendPhoneCode() {
                if (this.sendingCode) {
                    return
                }
                this.sendingCode = true
                this.sendPhoneCodeRequest()
            },
            setInterval() {
                let timer = setInterval(() => {
                    this.sendCodeOnces = false
                    this.sendCodeCount--
                    if (this.sendCodeCount <= 0) {
                        this.clearInterval(timer)
                    }
                }, 1000)
            },
            clearInterval(timer) {
                this.sendCodeCount = sendCodeCount
                this.sendingCode = false
                clearInterval(timer)
            }
        },
        components:{
            imgVerify
        },
        mounted(){
            let root = document.getElementsByTagName('html')[0]
            root.setAttribute('class','loginRoot')
        },
        created(){
            let root = document.getElementsByTagName('html')[0]
            root.setAttribute('class','loginRoot')
        },
        destroyed(){
            let root = document.getElementsByTagName('html')[0]
            root.setAttribute('class','')
        }
    }
</script>

<style lang="postcss" scoped>
    .btn-login {
        width: 100%;
        font-size: 1.1rem;

        &
        span {
            display: inline-block;
            letter-spacing: 2rem;
        }

    }
    .login-tab-item{
        cursor: pointer;
    }
</style>
